@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2.5rem;
}


.name {
  grid-column: 1 / 3;
  grid-row: 1 / 2;
}

.code {
  grid-column: 1 / 3;
  grid-row: 2 / 3;
}

.sizes {
  grid-column: 3 / 5;
  grid-row: 1 / 3;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2.5rem;
}

.size {
  grid-column: span 1;
}

.dual {
  grid-column: span 2;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2.5rem;
}

.sex {
  display: flex;
  align-items: center;
  margin-top: 2.5rem;

  span {
    @include fontSmall($colorDarkest);

    &:not(:first-child) {
      margin-left: 2rem;
    }
  }
}
