@import "styles/mixins";

.wrapper {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

.heading {
  @include h1;
}

.template {
  margin: 5rem auto 0 auto;
  width: 60%;
}

.button {
  @include resetButton;
  @include fontButtonSimple;

  margin-top: 2rem;
  padding: 1rem;
  background-color: $colorHighlightButton;
  transition-duration: $transitionDuration;

  &:hover {
    box-shadow: 0 0 1rem 0 $colorHighlightButton;
  }
}

.printable {
  display: none;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: 40rem;
  grid-gap: 1rem;
}

@media print {
  html {
    font-size: 5px !important;
  }

  .printable {
    display: grid;
  }

  .templatePrint {
    display: inline-block;
    height: 100%;
  }
}
