@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
}

.label {
  @include fontInputLabel;

  margin: 0 0 0.7rem 0;
  opacity: 0.7;
}

.select {
  width: 100%;

  :global {
    .simplebar-placeholder {
      display: block;
    }

    .select {
      &__control {
        height: 5rem;
        min-height: 5rem;
        border: 0.1rem solid $borderTabs;
        cursor: pointer;
        background-color: white;

        &:hover {
          border-color: $colorHighlightLight;
        }
      }

      &__control--is-disabled {
        .select__dropdown-indicator svg {
          fill: $colorHighlightLight;
        }
      }

      &__value-container {
        padding: 0 2rem;
        height: 100%;
      }

      &__input {
        @include fontDescriptor;
      }

      &__placeholder {
        @include fontDescriptor;

        opacity: 0.5;
      }

      &__single-value {
        @include fontDescriptor;

        left: 2rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
      }

      &__indicator-separator {
        display: none;
      }

      &__dropdown-indicator {
        padding: 0.8rem 2rem 0.8rem 2rem;

        svg {
          width: 2rem;
          height: 2rem;
          fill: $colorDarkest;
          transition-duration: $transitionDuration;
        }
      }

      &__menu {
        margin: 0;
        border-radius: 0;
        border-right: 1px solid $borderTabs;
        border-bottom: 1px solid $borderTabs;
        border-left: 1px solid $borderTabs;
        box-shadow: none;
      }

      &__menu-list {
        padding: 0;
      }

      &__option {
        @include fontDescriptor;

        height: 5rem;

        &:not(:first-child) {
          border-top: 0.1rem solid $borderTabs;
        }

        &:hover,
        &--is-focused {
          color: $colorHighlightLight;
        }

        &--is-selected {
          color: white;
          background-color: $colorHighlightLight;
          pointer-events: none;
        }

        &:not(.select__option--is-selected) {
          cursor:pointer;
        }
      }
    }
  }

  &:hover {
    svg {
      fill: $colorHighlightLight;
    }
  }
}
