@import "styles/variables";
@import "styles/mixins";

.wrapper {
  padding: 1.5rem;
  border: 1px dotted $colorDarkest;
  overflow: hidden;
}

.header {
  display: flex;
  align-items: center;
  height: 6rem;
  border-bottom: 1px solid $colorDarkest;
}

.icon {
  padding: 0;
  width: 7rem;
  height: 6rem;
  fill: $colorLightest;
  background-color: $colorDarkest;
}

.name {
  @include h2;

  margin-left: 2rem;
  text-transform: uppercase;
}

.body {
  --size-cell-width: 8.5rem;
  display: grid;
  grid-template-columns: calc(2 * var(--size-cell-width)) 30rem auto;
}

.sizes {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(2, var(--size-cell-width));
  grid-template-rows: repeat(3, var(--size-cell-width));
  border-right: 1px solid $colorDarkest;
}

.item {
  display: flex;
  flex-direction: column;
  padding: 1rem;

  &:not(:nth-child(2n)) {
    border-right: 1px solid $colorDarkest;
  }

  &:not(:last-child):not(:nth-last-child(2)) {
    border-bottom: 1px solid $colorDarkest;
  }
}

.key {
  @include fontAccent($colorDarkest);

  font-size: 2rem;
  line-height: 1;
}

.value {
  @include fontLink($colorDarkest);

  margin: auto 0 0 auto;
  font-size: 3.5rem !important;
  font-weight: 600;
  line-height: 1;

  :global {
    .size {
      display: flex;
      align-items: center;
    }

    .fraction {
      display: flex;
      flex-direction: column;
      margin-left: 0.5rem;
      font-size: 2rem;

      span {
        &:not(:first-child) {
          position: relative;
          margin-top: 0.5rem;

          &::before {
            content: "";
            position: absolute;
            top: -0.3rem;
            width: 100%;
            height: 0.3rem;
            background-color: $colorDarkest;
          }
        }
      }
    }
  }
}

.info {
  padding-left: 3rem;
  border-right: 1px solid $colorDarkest;
}

.model {
  @include h2;

  margin: 0;
  font-size: 3rem;
}

.label {
  @include fontAccent($colorDarkest);

  margin: 0;
  font-size: 1.6rem;
  line-height: 1.8rem;
}

.originals {
  display: block;
  margin-top: 2rem;
}

.manufacturer {
  @include fontDescriptor;

  display: block;
  margin-top: 3rem;
  font-size: 1.4rem;
  line-height: 1;
  text-transform: uppercase;
}

.error {
  @include fontDescriptor($colorHighlightLight);

  display: block;
  margin: 3rem 0 0 0;
  text-align: center;
}

.annex {
  @include fontDescriptor;

  margin-top: 0.5rem;
  padding-left: 2rem;
  font-size: 1.4rem;
  line-height: 1;

  @media print {
    margin-top: -10.5rem;
  }
}
