@import "styles/variables";
@import "styles/mixins";

.label {
  @include fontInputLabel;

  opacity: 0.7;
}

.input {
  @include fontDescriptor;

  display: block;
  margin: 0.5rem 0 0 0;
  padding: 0 2rem;
  width: 100%;
  height: 4rem;
  border: 1px solid $borderTabs;
  border-radius: 0.2rem;
  transition-duration: $transitionDuration;

  &:hover,
  &:focus {
    border-color: $colorHighlightLight;
  }
}

.textarea {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  min-height: 12.7rem;
  resize: none;
}
